import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import {
  Table,
  Button,
  Container,
  Modal,
  ModalHeader,
  ModalBody,
  FormGroup,
  ModalFooter,
} from 'reactstrap'

const data = [
  {Id: 1, Producto: 'Cocinero', Cantidad: 0 },
  {Id: 2, Producto: 'Ayudante General', Cantidad: 0 },
  {Id: 3, Producto: 'Contador', Cantidad: 0 },
  {Id: 4, Producto: 'Publicidad', Cantidad: 0 },
  ]


class App2 extends React.Component {
  state = {
    data: data,
    modalActualizar: false,
    modalInsertar: false,
    form: {
      Id: "",
      Producto: "",
      Cantidad: "",
    },
  };


  mostrarModalActualizar = (dato) => {
    this.setState({
      form: dato,
      modalActualizar: true,
    });
  };



  cerrarModalActualizar = () => {
    this.setState({ modalActualizar: false });
  };



  mostrarModalInsertar = () => {
    this.setState({
      modalInsertar: true,
    });
  };



  cerrarModalInsertar = () => {
    this.setState({ modalInsertar: false });
  };




  editar = (dato) => {
    var contador = 0;
    var arreglo = this.state.data;
    arreglo.map((registro) => {
      if (dato.Id == registro.Id) {
        arreglo[contador].Producto = dato.Producto;
        arreglo[contador].Cantidad = dato.Cantidad;
      }
      contador++;
    });
    this.setState({ data: arreglo, modalActualizar: false });
  };




  eliminar = (dato) => {
    var opcion = window.confirm('Estás Seguro que deseas Eliminar el elemento '+ dato.Id);
    if (opcion == true) {
      var contador = 0;
      var arreglo = this.state.data;
      arreglo.map((registro) => {
        if (dato.Id == registro.Id) {
          arreglo.splice(contador, 1);
        }
        contador++;
      });
      this.setState({ data: arreglo, modalActualizar: false });
    }
  };



  insertar= () => {
    var valorNuevo= {...this.state.form};
    valorNuevo.Id=this.state.data.length+1;
    var lista= this.state.data;
    lista.push(valorNuevo);
    this.setState({ modalInsertar: false, data: lista });
  }



  handleChange = (e) => {
    this.setState({
      form: {
        ...this.state.form,
        [e.target.name]: e.target.value,
      },
    });
  };





render() {
  return (
    <>
    <Container>
        <br/>
        <center><h1>Pagos</h1></center>
        <br/>
          <Button color="success" onClick={()=>this.mostrarModalInsertar()}>Crear</Button>
          <br/>
          <br/>
          <Table>
            <thead>
              <tr>
                <th>ID</th>
                <th>Producto</th>
                <th>Cantidad</th>
                <th>Acción</th>
              </tr>
            </thead>

            <tbody>
              {this.state.data.map((dato) => (
                <tr key={dato.Id}>
                  <td>{dato.Id}</td>
                  <td>{dato.Producto}</td>
                  <td>{dato.Cantidad}</td>
                  <td>
                    <Button
                      color="primary"
                      onClick={() => this.mostrarModalActualizar(dato)}
                    >
                      Editar
                    </Button>{" "}
                    <Button color="danger" onClick={()=> this.eliminar(dato)}>Eliminar</Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </Container>

        <Modal isOpen={this.state.modalActualizar}>
          <ModalHeader>
           <div><h3>Editar Registro</h3></div>
          </ModalHeader>

          <ModalBody>
            <FormGroup>
              <label>
               Id:
              </label>
            
              <input
                className="form-control"
                readOnly
                type="text"
                value={this.state.form.Id}
              />
            </FormGroup>
            
            <FormGroup>
              <label>
                Producto: 
              </label>
              <input
                className="form-control"
                name="Producto"
                type="text"
                onChange={this.handleChange}
                value={this.state.form.Producto}
              />
            </FormGroup>
            
            <FormGroup>
              <label>
                Cantidad: 
              </label>
              <input
                className="form-control"
                name="Cantidad"
                type="number"
                onChange={this.handleChange}
                value={this.state.form.Cantidad}
              />
            </FormGroup>
          </ModalBody>

          <ModalFooter>
            <Button
              color="primary"
              onClick={() => this.editar(this.state.form)}
            >
              Editar
            </Button>
            <Button
              color="danger"
              onClick={() => this.cerrarModalActualizar()}
            >
              Cancelar
            </Button>
          </ModalFooter>
        </Modal>



        <Modal isOpen={this.state.modalInsertar}>
          <ModalHeader>
           <div><h3>Insertar Producto</h3></div>
          </ModalHeader>

          <ModalBody>
            <FormGroup>
              <label>
                Id: 
              </label>
              
              <input
                className="form-control"
                readOnly
                type="text"
                value={this.state.data.length+1}
              />
            </FormGroup>
            
            <FormGroup>
              <label>
                Producto: 
              </label>
              <input
                className="form-control"
                name="Producto"
                type="text"
                onChange={this.handleChange}
              />
            </FormGroup>
            
            <FormGroup>
              <label>
                Cantidad: 
              </label>
              <input
                className="form-control"
                name="Cantidad"
                type="number"
                onChange={this.handleChange}
              />
            </FormGroup>
          </ModalBody>

          <ModalFooter>
            <Button
              color="primary"
              onClick={() => this.insertar()}
            >
              Insertar
            </Button>
            <Button
              className="btn btn-danger"
              onClick={() => this.cerrarModalInsertar()}
            >
              Cancelar
            </Button>
          </ModalFooter>
        </Modal>

  </>
  )
}
}
export default App2